import Vue from 'vue'
import Router from 'vue-router'
import homePage from '@/views/homepage/homepage' //
import lease from '@/views/homepage/lease' //
import serve from '@/views/homepage/serve' //
import destination_serve from '@/views/homepage/destination_serve' //
import city from '@/views/homepage/city' //
import end_city from '@/views/homepage/end_city' //


import serve_details from '@/views/homepage/serve_details' //
import details from '@/views/homepage/details' //
import order_form from '@/views/homepage/order_form' //


import mine from '@/views/minepage/mine' //
import myOrder from '@/views/minepage/myOrder' //
import order_details from '@/views/minepage/order_details' //
import shop_car from '@/views/minepage/shop_car' //
import friend from '@/views/minepage/friend' //
import my_discounts from '@/views/minepage/my_discounts' //
import FAQ from '@/views/minepage/FAQ' //
import feedback from '@/views/minepage/feedback' //
import about from '@/views/minepage/about' //
import my_news from '@/views/minepage/my_news' //
import message from '@/views/minepage/message' //
import set from '@/views/minepage/set' //
import set_phone from '@/views/minepage/set_phone' //
import amend_psd from '@/views/minepage/amend_psd' //
import car_order from '@/views/minepage/car_order' //
import identity from '@/views/minepage/identity' //




import login from '@/views/loginpage/login' //
import register from '@/views/loginpage/register' //
import forget_psd from '@/views/loginpage/forget_psd' //
import verify_phone from '@/views/loginpage/verify_phone' //




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homePage',
      component: homePage,
      meta: {
          keepAlive: true
      }
    },
    {
      path: '/details',
      name: 'details',
      component: details
    },
    {
      path: '/lease',
      name: 'lease',
      component: lease
    },
    {
      path: '/order_form',
      name: 'order_form',
      component: order_form
    },
    {
      path: '/serve',
      name: 'serve',
      component: serve
    },
    {
      path: '/destination_serve',
      name: 'destination_serve',
      component: destination_serve
    },
    {
      path: '/serve_details',
      name: 'serve_details',
      component: serve_details
    },
    {
      path: '/city',
      name: 'city',
      component: city
    },
    {
      path: '/end_city',
      name: 'end_city',
      component: end_city
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    },
    {
      path: '/myOrder',
      name: 'myOrder',
      component: myOrder
    },
    {
      path: '/order_details',
      name: 'order_details',
      component: order_details
    },
    {
      path: '/shop_car',
      name: 'shop_car',
      component: shop_car
    },
    {
      path: '/friend',
      name: 'friend',
      component: friend
    },
    {
      path: '/my_discounts',
      name: 'my_discounts',
      component: my_discounts
    },
    {
      path: '/FAQ',
      name: 'FAQ',
      component: FAQ
    },
    {
      path: '/identity',
      name: 'identity',
      component: identity
    },
    {
      path: '/feedback',
      name: 'feedback',
      component: feedback
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/my_news',
      name: 'my_news',
      component: my_news
    },
    {
      path: '/message',
      name: 'message',
      component: message
    },
    {
      path: '/set',
      name: 'set',
      component: set
    },
    {
      path: '/car_order',
      name: 'car_order',
      component: car_order
    },
    {
      path: '/set_phone',
      name: 'set_phone',
      component: set_phone
    },
    {
      path: '/amend_psd',
      name: 'amend_psd',
      component: amend_psd
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/forget_psd',
      name: 'forget_psd',
      component: forget_psd
    },
    {
      path: '/verify_phone',
      name: 'verify_phone',
      component: verify_phone
    }
  ]
})



